body {
  margin: 0;
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  word-wrap: break-word;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  color: #ffffff;
  line-height: 24px; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
  font-style: normal;
  font-display: swap;
  font-weight: 400; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-700.woff2") format("woff2"), url("../fonts/roboto-condensed-700.woff") format("woff");
  font-style: normal;
  font-display: swap;
  font-weight: 700; }

@font-face {
  font-family: "PT Mono";
  src: url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff");
  font-style: normal;
  font-display: swap;
  font-weight: 400; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
  font-style: normal;
  font-display: swap;
  font-weight: 400; }

.wrapper {
  margin-left: auto;
  margin-right: auto;
  max-width: 1471px;
  padding: 0 30px; }

.page-header {
  padding: 32px 0 0 0;
  margin-bottom: 95px;
  background-color: #1b2d37;
  background-repeat: no-repeat;
  background-image: url("../img/bg-lines.png");
  background-size: cover; }

.main-nav {
  margin-bottom: 174px; }

.main-nav__inner {
  display: flex;
  justify-content: space-between; }

.main-nav__right {
  display: flex; }

.main-nav__call-request {
  color: #ffffff; }

@media (max-width: 1023px) {
  .main-nav__call-request {
    display: none; } }

.main-logo {
  display: flex;
  align-items: center;
  background-color: flex-start; }

.main-logo img {
  display: block;
  margin-right: 18px; }

.main-logo__wrapper {
  padding-top: 5px; }

.main-logo__title {
  margin: 0 0 2px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  line-height: 1.15;
  font-weight: 700; }

.main-logo__tagline {
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  color: rgba(255, 255, 255, 0.5);
  text-transform: lowercase;
  font-size: 12px;
  line-height: 1.16; }

.contacts-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  margin-right: 50px; }

.contacts-list__item {
  padding-top: 7px; }

.contacts-list__item:not(:last-child) {
  margin-right: 80px; }

.contacts-list__link {
  position: relative;
  display: block;
  padding-top: 14px;
  padding-left: 40px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #ffffff;
  transition-property: color;
  transition-duration: 0.25s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  font-size: 16px;
  line-height: 1.12; }
  .contacts-list__link:hover, .contacts-list__link:focus {
    color: rgba(255, 255, 255, 0.6);
    outline: none; }

.contacts-list__link svg {
  position: absolute; }

.contacts-list__link--mail svg {
  left: 1px;
  bottom: 4px; }

.contacts-list__link--phone svg {
  left: 7px;
  bottom: 2px; }

@media (max-width: 1023px) {
  .contacts-list__link {
    font-size: 0; }
  .contacts-list__link svg {
    width: 24px;
    height: 24px;
    outline: 1px solid; } }

.promo {
  padding-top: 60px;
  background-image: url("../../img/curcuits@1x.png");
  background-repeat: no-repeat;
  background-size: 705px 515px;
  background-position: 83% 0; }

.promo__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-right: 35%; }

.promo__title {
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.07em;
  margin-bottom: 30px;
  font-size: 80px;
  line-height: 1.17;
  font-weight: 700; }

.promo__description {
  position: relative;
  margin: 0;
  padding-left: 94px;
  margin-bottom: 44px;
  font-family: "PT Mono", "Arial", sans-serif;
  text-transform: lowercase;
  font-size: 22px;
  line-height: 1.54; }
  .promo__description::before {
    content: "";
    display: block;
    width: 66px;
    height: 2px;
    background-color: #e5e5e5;
    position: absolute;
    left: 0;
    top: 14px; }

.promo__btn {
  margin-left: 95px;
  width: 405px;
  margin-bottom: 275px; }

.btn {
  border-radius: 5px;
  padding: 18px 28px;
  text-align: center;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-size: 16px;
  line-height: 1.18; }

.btn--filled {
  border: none;
  background-color: #00b2ff;
  color: #ffffff;
  transition-property: background-color;
  transition-duration: 0.25s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  .btn--filled:hover, .btn--filled:focus {
    background-color: #ffffff;
    color: #000000;
    outline: none; }

.btn--bordered {
  font-size: 14px;
  line-height: 1.14;
  transition-property: background-color;
  transition-duration: 0.25s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  padding: 18px 28px;
  border: 1px solid #ffffff; }
  .btn--bordered:hover, .btn--bordered:focus {
    background-color: #ffffff;
    color: #000000;
    outline: none; }

.scroll-down {
  position: absolute;
  left: -22px;
  bottom: 130px;
  width: 120px;
  background-color: transparent;
  border: none;
  font-family: "PT Mono", "Arial", sans-serif;
  color: rgba(255, 255, 255, 0.5);
  transform: rotate(-90deg);
  font-size: 14px;
  line-height: 1.14;
  transition-property: color;
  transition-duration: 0.25s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  .scroll-down:hover, .scroll-down:focus {
    outline: none;
    color: rgba(255, 255, 255, 0.6); }

.features__inner {
  outline: 1px solid blue; }

.features__list {
  display: flex;
  flex-wrap: wrap;
  padding-left: 120px; }

.feature-card {
  display: flex;
  flex-direction: column;
  width: 570px;
  min-height: 299px;
  outline: 1px solid red;
  margin-bottom: 30px;
  padding: 100px 0 0 45px; }

.feature-card:not(:nth-child(2n)) {
  margin-right: 35px; }

.feature-card__title {
  margin: 0 0 20px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #00b2ff;
  font-size: 24px;
  line-height: 1.41; }

.feature-card__description {
  margin: 0 0 65px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #666666;
  font-size: 16px;
  line-height: 1.37; }
